section {
    display: flex;
    flex-direction: column;
    height: 70vh;
    margin: auto;
    text-align: center;
    font-family: "Play", sans-serif;
    width: 100%;
    color: white;
    font-weight: lighter;
}
#root{
    background-image: url('../../pics/adult-backlit-beach.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.title{
    display: flex;
    height: 100px;
    width: 100%;
    font-size: 4em;
    margin: 0;
}

.title p {
    margin: auto;
 }

.intro{
    font-size: 3em;
    margin-top: 0px;
    width: 100%;
    
}

.signup-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 60%;
    margin-top: 200px;
}

.signup{
    width: 250px;
    height: 50px;
    border: transparent;
    border-radius: 10px;
    background-image: linear-gradient(to right, #A731A5, #550554);
    text-transform: uppercase;
    font-size: 1.5em;
    font-weight: bold;
    color: white;
}

.local-icon{
    display: inline-flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 2em;
    width: 100%;
    height: 100px;
    margin-top: 50px;
}

.icon{
    margin-left: 30px;
    margin-right: 50px;
}



