.user-pointer {
    position: absolute;
    font-size: 3em;
    margin-top: 50px;
    margin-left: 70px;
    color: white;
}

a {
    font-weight: bold;
    text-decoration: none;
    color: #000;
}

#signup {
    padding-left: 5px;
    display: block;
    transition: transform 0.2s ease-out, color 0.2s linear;
}

#signup:hover {
    color: #b3442e;
    transform: scale(1.1);
}

main section {
    width: 100%;
    height: 90vh;
    overflow: hidden;
}
section canvas {
    position: relative !important;
}

.mapboxgl-marker i {
    color: #e27cdf;
    font-size: 22px;
    padding: 0px;
    position: relative;
    top: -20px;
    left: -8px;
    transition: transform 0.3s ease-in-out;
}

.mapboxgl-marker #me {
    color: blue;
    font-size: 45px;
    top: -40px;
    left: -18px;

}

.mapboxgl-marker i:hover {
    transform: scale(1.3);
}

#popup {
    position: absolute;
    width: 50vw;
    height: 0px;
    bottom: 0px;
    border-radius: 20px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    left: 25%;
    background-color: #1f3a9388;
    color: black;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    justify-content: center;
    overflow: hidden;
    transition: height 0.6s ease-in-out;
}


#popup div{
    text-align: center;
    width: 40%;
    height: 50px;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    border-radius: 18px;
    border: none;
    font-weight: bold;
    background-color: #1f3a93aa;
}
#popup div * {
    width: 90%;
    background-color: transparent;
    border: none;
    color: white;
}
#popup div label{
    font-size: 10px;
    text-align: left;
}
#popup div input{
    height: 20px;
    font-size: 15px;
    text-align: center;
    outline: none;
}

#add-new {
    position: absolute;
    width: 50vw;
    height: 0px;
    bottom: 0px;
    border-radius: 20px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    left: 25%;
    background-color: #1f3a9388;
    color: black;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    overflow: hidden;
    transition: height 0.4s ease-in-out;
}

#add-new input {
    height: 30px;
    width: 50%;
    margin: 5px auto;
    border-radius: 15px;
    text-align: center;
}

#add-new input[type="button"] {
    background-color: #1f3a93;
    color: white;
    font-weight: bold;
    border: none;
    height: 40px;
}
#close-btn {
    display:flex;
    width: 40px;
    height: 40px;
    cursor: pointer;
    position: absolute;
    left: 0px;
    top: 0px;
    color: white;
    background-color: #0000ff66;
    border-radius: 50%;
    border-bottom-left-radius: 0px;
    border-top-right-radius: 0px;
    align-items: center;
    justify-content: center;
}
@media (max-width: 900px){
    main{
        width: 100%;
    }
}